import { _decorator, Component, Node, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('bgTransForm')
export class bgTransForm extends Component {


	moveBg() {

		let bgList = this.node.children || []

		for (let i of bgList){
			


			let y = i.position.y += -3


			bgList[i].setPosition(new Vec3(0, y, 0));

			// if (bgList[i].position.y <= -1500) {
			// 	bgList[i].position.y = 1500
			// }
			// console.log(bgList[i].position.y, '000-=-=-=-=-=')
			




		}


	}

	// 1、先执行 - 首次加载
	onLoad() {
		setInterval(() => {
			this.moveBg()
		},1500)
	}
	// 2、执行
	start() {

	}
	// 每帧都执行
	update(deltaTime: number) {
		// this.moveBg()
	}
}

